.membership {
    .section__block {
        .section__sort {
            background-color: $bg-very-light;
            padding: 10px 20px;
            margin-top: 8px;
            text-align: center;
            .link {
                margin-right: 3px; } }
        .section__nav {
            margin: 15px 0 7px 0;
            .multiple-select {
                border: 1px solid $border-gray; }
            .section__icons {
                float: right;
                display: inline-block;
                overflow: hidden;
                .icon {
                    width: 32px;
                    height: 32px;
                    border: 1px solid $border-gray; } } }

        .section__content {
            .users {
                border: 1px solid $border-gray;
                padding: 12px 0;
                .user {
                    padding: 5px 12px;
                    @include clr;
                    &:hover {
                        background-color: $bg-light; }
                    .user__name {
                        float: left;
                        display: inline-block;
                        padding: 5px 0 0 5px; }
                    .icon {
                        float: left;
                        width: 25px;
                        height: 25px; }
                    .user__contact {
                        float: right; }
                    .user__info {
                        @include vertical;
                        float: right;
                        display: inline-block;
                        background-color: $bg-blue;
                        padding: 2px 5px;
                        margin: 5px 7px 0 0;
                        font-family: $font-regular;
                        font-size: em(10, 13);
                        font-weight: 400;
                        color: #fff; } } } } } }


